<template>
  <div class="loginView">
    <form class="bd">
      <ul>
        <li><span>账号:</span><input type="text" v-model="registerName" /></li>
        <li>
          <span>密码:</span><input type="password" v-model="registerPassword" />
        </li>
        <li>
          <span>确认密码:</span><input type="password" v-model="affirmPsw" />
        </li>
        <li><div class="send" @click="sends">确认注册</div></li>
      </ul>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      registerName: "",
      registerPassword: "",
      affirmPsw: "",
    };
  },
  methods: {
    sends() {
      let newUserData = JSON.parse(localStorage.getItem('account'))||[];
      let UserData = {
        name: this.registerName,
        pasw: this.registerPassword,
        affirmpasw: this.affirmPsw,
      };
      newUserData.push(UserData);
      localStorage.setItem("account", JSON.stringify(newUserData));
    },
  },
};
</script>

<style scoped>
.loginView {
  width: 100%;
  height: 100vh;
  background-color: rgb(245, 162, 162);
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 345px;
}
.bd {
  box-sizing: border-box;
  padding: 50px;
  border: 2px solid white;
  border-radius: 13px;
}
.bd li {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  height: 65px;
}
.bd li span {
  display: block;
  font: 700 1rem "";
  width: 27%;
}
.bd input {
  outline: none;
  border: 0;
  height: 40px;
  margin-left: 10px;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 10px;
}
.bd .send {
  box-sizing: border-box;
  padding: 10px;
  border: 1px solid white;
  margin-top: 25px;
  border-radius: 12px;
  cursor: pointer;
  user-select: none;
}
</style>
